import React, { Component } from 'react'
import logo from './logo.svg'
import './App.css'
import DrumMachine from './DrumMachine'
import {drumPadButtons} from './buttonsData.json'
import DrumPad from './DrumPad'


class App extends Component {
  constructor(){
    super()
    this.state = {
      buttonsData: drumPadButtons
    }
    this.playButton = this.playButton.bind(this)
  }

  playButton(event) {
    const {lastChild} = event.target
    console.log(lastChild)
  }

  render() {
    
    const drumPads = this.state.buttonsData.map(button => 
      <DrumPad 
        id= {button.letter}
        key={button.letter}
        audioId = {button.sound}
        sound = {'./' + button.sound}
        playButton = {this.playButton}
      />
      )
    return (
        <DrumMachine 
          logo={logo}
          drumPads={drumPads}
        />
    );
  }
}

export default App;
