import React from 'react'


function DrumPad(props) {
    
    return (
        <div className="drum-pad" id={props.audioId} onClick={props.playButton}>
            {props.id}
            <audio className="clip" id={props.id} src={props.sound} type="audio/wav">
                This browser does not support the audio tag
            </audio>
        </div>
    )
}

export default DrumPad