import React from 'react'

function DrumMachine(props) {
    console.log(props.sound)
    return (
        <div id="drum-machine">
            <img src={props.logo} className="App-logo" alt="logo" />
            <h1>Steven's Drum Machine</h1>
            <br></br>
            <div id="display">
                {props.drumPads}
            </div>
        </div>
    )
}

export default DrumMachine 